<template>
  <div>
    <div class="nav-bar noshadow">
      <div class="nav-bar-left" @click="goBack()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-search">
        <div class="header-search">
          <search-box ref="searchBox" @query="onQueryChange" />
        </div>
      </div>
    </div>
    <!--    class="shortcut-wrapper"-->
    <div ref="shortcutWrapper" class="container">
      <!-- 搜索 begin -->
      <div class="search-content">
        <div class="search-list">
          <div class="title">
            <div class="title-left">
              <h2>热门搜索</h2>
            </div>
          </div>
          <ul>
            <search-list :searches="hotKey" @delete="deleteSearchHistory" @select="addQuery" />
          </ul>
        </div>

        <!-- <div class="search-list">
          <div class="title">
            <div class="title-left">
              <h2>搜索历史</h2>
            </div>
            <div class="title-right" @click="showConfirm()"><i class="iconfont iconshanchu1" /></div>
          </div>
          <search-list :searches="searchHistory" @delete="deleteSearchHistory" @select="addQuery" />
          <ul />
        </div> -->
      </div>
    </div>
    <!-- <div v-show="query" ref="searchResult" class="container">
      <suggest ref="suggest" :query="query" @listScroll="blurInput" @select="saveSearch" />
    </div> -->
    <confirm ref="confirm" text="是否清空所有搜索历史" confirm-btn-text="清空" @confirm="clearSearch" />
  </div>
</template>

<script>
// import headTop from '../../components/header/head'
import SearchBox from '@/components/search/search-box'
import SearchList from '@/components/search/search-list'
// import Scroll from '../../../../base/scroll/scroll'
import Confirm from '@/components/confirm/confirm'
import { getSsHotwordsList, addSsHotwords } from '@/store/module.js'
import { mapActions, mapState } from 'vuex'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'Search',
  components: {
    SearchBox,
    SearchList,
    Confirm
  },
  // mixins: [pageRouter],
  data() {
    return {
      hotKey: [],
      query: '',
      searchHistory: []
    }
  },
  computed: {
    // ...mapGetters([
    //   'searchDbsxList'
    // ]),
    ...mapState({
      searchHostyList: state => state.jbrsearch.searchgysWdxmList
    }),
    shortcut() {
      return this.hotKey.concat(this.searchDbsxList)
    }
  },
  watch: {
    query(newQuery) {
      if (!newQuery) {
        setTimeout(() => {
          // this.$refs.shortcut.refresh()
          const test1 = this.searchHostyList
          this.searchHistory = test1[0][1]
        }, 20)
      }
    }
  },
  created() {
    // 获取推荐数据
    this._getHotKey()
    // this.searchHostyList = this.searchDbsxHistory

    const test1 = this.searchHostyList
    if (test1 != null && test1 !== '') {
      this.searchHistory = test1[0][1]
    }
  },
  methods: {
    goBack() {
      this.$router.push('MallHome')
    },
    ...mapActions([
      'updateJbrSearchList',
      'clearJbrSearchHistory',
      'deleteSearchHistory'
    ]),
    showConfirm() {
      this.$refs.confirm.show()
    },
    onQueryChange(query) {
      // 保存/更新热词
      addSsHotwords.data = { content: query }
      this.asyncApiModule(addSsHotwords).then(res => {
      })
      this.query = query
      this.$store.commit('goods/SET_INPUT', query)
      this.$bus.$emit('searchByInput', query)
      this.$router.push({
        name: 'GoodsList',
        params: {
          searchInput: query
        }
      })
    },
    blurInput() {
      this.$refs.searchBox.blur()
    },
    addQuery(query) {
      this.$refs.searchBox.setQuery(query)
    },
    clearSearch() {
      this.clearJbrSearchHistory({ id: '3' })
    },
    saveSearch() {
      // if (this.query != null && this.query !== '') {
      this.updateJbrSearchList({ key: '3', query: this.query })
      // }
    },
    _getHotKey() {
      this.asyncApiModule(getSsHotwordsList).then(res => {
        for (let i = 0; i < res.length; i++) {
          this.hotKey.push(res[i].content)
        }
      })
        .catch(() => {
          this.loading = false
        })
    }

  }

}
</script>

